import Vue from 'vue';
import VueRouter from 'vue-router';
import HomeView from '../views/HomeView.vue';
import ThankYou from '../views/ThankYou.vue';
// import ThankYou2 from '../views/ThankYou2.vue';
import AboutUs from '../views/AboutUs.vue'
import Gallery from '../views/Gallery.vue'
import ContactUs from '../views/ContactUs.vue'
import Whitening from '../views/Whitening.vue'
import Filling from '../views/Filling.vue'
import Orthodontic from '../views/Orthodontic.vue'
import HollywoodSmile from '../views/HollywoodSmile.vue'
import Extractions from '../views/Extractions.vue'
import Veneers from '../views/Veneers.vue'
import RootCanal from '../views/RootCanal.vue'
import OurDoctors from '../views/OurDoctors.vue'

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView,
    meta: { title: 'Home' }
  },
  {
    path: '/thankyou',
    name: 'thankyou',
    component: ThankYou,
    meta: { title: 'Thank You Page' }
  },
  {
    path: '/about',
    name: 'About',
    component: AboutUs,
    meta: { title: 'About us' }
  },
  {
    path: '/gallery',
    name: 'Gallery',
    component: Gallery,
    meta: { title: 'Gallery' }
  },
  {
    path: '/contact',
    name: 'Contact',
    component: ContactUs,
    meta: { title: 'Contact us' }
  },
  {
    path: '/whitening',
    name: 'Whitening',
    component: Whitening,
    meta: { title: 'Whitening' }
  },
  {
    path: '/filling',
    name: 'Filling',
    component: Filling,
    meta: { title: 'Filling' }
  },
  {
    path: '/Orthodontic',
    name: 'Orthodontic',
    component: Orthodontic,
    meta: { title: 'Orthodontic' }
  },
  {
    path: '/hollywoodSmile',
    name: 'HollywoodSmile',
    component: HollywoodSmile,
    meta: { title: 'HollywoodSmile' }
  },
  {
    path: '/extractions',
    name: 'Extractions',
    component: Extractions,
    meta: { title: 'Extractions' }
  },
  {
    path: '/veneers',
    name: 'Veneers',
    component: Veneers,
    meta: { title: 'Veneers' }
  },
  {
    path: '/rootcanal',
    name: 'RootCanal',
    component: RootCanal,
    meta: { title: 'RootCanal' }
  },
  {
    path: '/ourdoctors',
    name: 'OurDoctors',
    component: OurDoctors,
    meta: { title: 'OurDoctors' }
  },
];

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
});

router.beforeEach((to, from, next) => {
  if (to.meta.title) {
    document.title = `${to.meta.title} - Floraisondental`;
  } else {
    document.title = 'Floraisondental';
  }
  next();
});

export default router;
