<template>
     <!-- Contact Start -->
     <div class="container-fluid py-5" id="contact">
      <div class="container">
        <div class="row g-5">
          <div class="col-xl-4 col-lg-6 wow slideInUp" data-wow-delay="0.1s"
            style="visibility: visible; animation-delay: 0.1s; animation-name: slideInUp;">
            <div class="bg-light rounded h-100 p-5">
              <div class="section-title">
                <h5 class="position-relative d-inline-block text-primary text-uppercase">Contact Us</h5>
                <h1 class="display-6 mb-4">Feel Free To Contact Us</h1>
              </div>
              <div class="d-flex align-items-center mb-2">
                <i class="bi bi-geo-alt fs-1 text-primary me-3"></i>
                <div class="text-start">
                  <h5 class="mb-0">Our Office</h5>
                  <span>2020 Building, 1st Floor, Office No- 123, Sheikh Zayed Road, Al Qouz 3 Dubai, UAE</span>
                </div>
              </div>
              <div class="d-flex align-items-center mb-2">
                <i class="bi bi-envelope-open fs-1 text-primary me-3"></i>
                <div class="text-start">
                  <h5 class="mb-0">Email Us</h5>
                  <span>info@floraisondental.com</span>
                </div>
              </div>
              <div class="d-flex align-items-center">
                <i class="bi bi-phone-vibrate fs-1 text-primary me-3"></i>
                <div class="text-start">
                  <h5 class="mb-0">Call Us</h5>
                  <span>+04 451 7533</span>
                </div>
              </div>
            </div>
          </div>
          <div class="col-xl-4 col-lg-6 wow slideInUp" data-wow-delay="0.3s"
            style="visibility: visible; animation-delay: 0.3s; animation-name: slideInUp;">
            <form @submit.prevent="submitForm">
              <div class="row g-3">
                <div class="col-12">
                  <input v-model="formData.name" type="text" class="form-control border-0 bg-light px-4"
                    placeholder="Your Name" style="height: 55px;">
                </div>
                <div class="col-12">
                  <input v-model="formData.email" type="email" class="form-control border-0 bg-light px-4"
                    placeholder="Your Email" style="height: 55px;">
                </div>
                <div class="col-12">
                  <textarea v-model="formData.message" class="form-control border-0 bg-light px-4 py-3" rows="5"
                    placeholder="Message"></textarea>
                </div>
                <div class="col-12">
                  <button class="btn btn-primary w-100 py-3" type="submit">Send Message</button>
                </div>
              </div>
            </form>
          </div>
          <div class="col-xl-4 col-lg-12 wow slideInUp" data-wow-delay="0.6s"
            style="visibility: visible; animation-delay: 0.6s; animation-name: slideInUp;">
            <iframe class="position-relative rounded w-100 h-100"
              src="https://maps.google.com/maps?q=2020+Building%2C+1st+Floor%2C+Office+No-+123%2C+Sheikh+Zayed+Road%2C+Al+Qouz+3+Dubai%2C+UAE&t=&z=13&ie=UTF8&iwloc=&output=embed"
              frameborder="0" style="min-height: 400px; border:0;" allowfullscreen="" aria-hidden="false"
              tabindex="0"></iframe>

          </div>
        </div>
      </div>
    </div>
    <!-- Contact End -->
</template>
<script>

import axios from 'axios';

export default {
    name: 'Contact',
  data() {
    return {
      formData: {
        name: '',
        email: '',
        message: ''
      },
      appointmentData: {
        name: '',
        phone: '',
        email: '',
        time: '',
        service: '',
        message: ''
      },
      responseMessage: ''
    };
  },
  methods: {
    async submitForm() {
      console.log(this.formData)
      try {

        const response = await axios.get('https://floraisondental.alruya.link/admin/contact.php', this.formData);
        this.responseMessage = response.data.message;

        // Redirect to the thank you page
        this.$router.push('/thankyou2');
      } catch (error) {
        this.responseMessage = 'An error occurred. Please try again.';
      }
    },
    async appointmentForm() {
      console.log(this.formData)
      try {

        const response = await axios.get('https://floraisondental.alruya.link/admin/appointment.php', this.appointmentData);
        this.responseMessage = response.data.message;

        // Redirect to the thank you page
        this.$router.push('/thankyou');
      } catch (error) {
        this.responseMessage = 'An error occurred. Please try again.';
      }
    }

  }
};
</script>